<template>
	<view class="register">
		<u-navbar back-icon-color="#fff" title-color="#fff" title="我的物品" :background="background" title-bold>
			<view slot="right" style="margin-right: 30rpx;" @click="addThings">
				<u-icon name="plus-circle" style="margin-right: 7rpx;"></u-icon>添加物品
			</view>
		</u-navbar>
		<view class="mybox">
			<u-avatar :src="header" size="100"></u-avatar>
			<p style="font-weight: bold;
color: #333333;margin-top: 17rpx;">{{name}}</p>
			<p style="font-size: 24rpx;color: #999;margin-top: 12rpx;" @click="toBody">身材数据<u-icon name="arrow-right"
					style="margin-left: 8rpx;"></u-icon></p>
		</view>
		<view class="formBox">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				我的物品
			</view>
			<view class="list02" v-for="(item,index) in list01" :key="index">
				<u-row>
					<u-col span="2">
						<u-avatar :src="item.file" size="90"></u-avatar>
					</u-col>
					<u-col span="8">
						<p class="titlebox">{{item.goodsName}}</p>
						<p class="pbox">规格：{{item.untitled1}} 数量：{{item.untitled2}}</p>
						<p class="pbox">领取时间：{{item.untitled3}}</p>
					</u-col>
					<u-col span="2" style="text-align: right;">
						<p class="pbox blue" @click="delthings(item.id)">删除</p>
					</u-col>
				</u-row>
			</view>
		</view>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		article,
		getarticle,
		delarticle
	} from '../../api/index.js'
	export default {
		data() {
			return {
				background: {
					backgroundColor: '#1F61BE',
					color: "#fff"
				},
				list01: [],
				name:uni.getStorageSync('userInfo').universalAntiUserMain27,
				header:uni.getStorageSync('userInfo').userFile,
			}
		},
		onLoad() {
			this.getList();
		},
		methods: {
			getList() {
				let data=[];
				data.queryJson = [{
					data1: 'user_id',
					data2: '0',
					data3: uni.getStorageSync('userInfo').userId,
					objectId: "292",
					subType: null,
					flag: false,
					fileType: "String"
				}];
				data.queryJson = JSON.stringify(data.queryJson);
				article(data).then(res => {
					if (res.code == 200) {
						this.list01 = res.rows;
					}
				});
			},
			addThings() {
				uni.navigateTo({
					url: 'addgoods'
				})
			},
			toBody() {
				uni.navigateTo({
					url: 'body'
				})
			},
			delthings(idc) {
				delarticle(idc).then(res => {
					if (res.code == 200) {
						this.$refs.uToast.show({
							title: '删除成功'
						});
						this.getList();
					}
				});
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
	}

	.mybox {
		text-align: center;
		padding: 50rpx 0;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.list02 {
		width: 100%;
		height: 180rpx;
		background: #FFFFFF;
		border-bottom: 1px solid #EEEEEE;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
		padding: 26rpx 0 28rpx 0;
	}

	.list02 .titlebox {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
	}

	.list02 .pbox {
		margin-top: 8rpx;
		font-size: 24rpx;
		color: #999999;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 .blue {
		color: #4490FB;
	}


	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}
</style>